<tr>
<td>styleId</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> The Mapbox Style ID.</td>
</tr>
<tr>
<td>accessToken</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> 	The public access token for the imagery.</td>
</tr>
<tr>
<td>tilesize</td>
<td>Number</td>
<td><code v-pre>512</code></td>
<td><code v-pre>optional</code> The size of the image tiles.</td>
</tr>
<tr>
<td>scaleFactor</td>
<td>Boolean</td>
<td></td>
<td><code v-pre>optional</code> Determines if tiles are rendered at a @2x scale factor.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The ellipsoid. If not specified, the WGS84 ellipsoid is used.</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> The minimum level-of-detail supported by the imagery provider. Take care when specifying this that the number of tiles at the minimum level is small, such as four or less. A larger number is likely to result in rendering problems.</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 	The maximum level-of-detail supported by the imagery provider, or undefined if there is no limit.</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The rectangle, in radians, covered by the image. <strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
